<script>
  import { fade } from 'svelte/transition'
  import Topbar from '../orgs/Topbar.svelte'
  import Sidenav from '../orgs/Sidenav.svelte'
  import Footer from '../orgs/Footer.svelte'

  let openSidenav = false
</script>

<style>
  div {
    @apply flex;
  }
  main {
    @apply h-full w-full m-auto pt-20 px-4 flex flex-col;
  }
</style>

<Topbar {openSidenav} on:click={() => openSidenav = !openSidenav} on:reset={() => openSidenav = false}/>

<div>
  <Sidenav {openSidenav}/>

  {#if !openSidenav}
    <main in:fade="{{ duration: 200, delay: 300 }}"> 
      <slot>
        <div class="content">content</div>
      </slot>

      <Footer/>
    </main>
  {/if}
</div>

